@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box;font-family:poppins,Arial,sans-serif}.signup-page{background-image:url("https://images.pexels.com/photos/6469/red-hands-woman-creative.jpg");background-size:cover;background-repeat:no-repeat;background-position:50% 50%}.signup-page .overlay{position:absolute;top:0;left:0;right:0;bottom:0;content:'';opacity:.8;background:linear-gradient(45deg, #f200ff 0%, #ffba42 100%)}.container{max-width:1210px}.activeClassName{font-weight:bolder;color:#ffd369 !important}input,textarea{width:100%;outline:0;border-width:0 0 1px;border-color:#d3d3d3}input:focus,textarea:focus{border-color:black}input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{font-size:small;font-weight:lighter}input:-ms-input-placeholder, textarea:-ms-input-placeholder{font-size:small;font-weight:lighter}input::-ms-input-placeholder, textarea::-ms-input-placeholder{font-size:small;font-weight:lighter}input::placeholder,textarea::placeholder{font-size:small;font-weight:lighter}input.error,textarea.error{border-color:red}.btn{background:#ffba42}.btn:hover{background:#ef9800;-webkit-transition:all 0.45s ease;transition:all 0.45s ease}.btn:active{background:#c27b00}.password-trigger{position:absolute;top:50%;right:.5rem;margin-right:45px;z-index:9999;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.password-trigger:hover{cursor:pointer}.signup-form-wrapper{border-radius:20px;box-shadow:0 15px 16.83px 0.17px rgba(0,0,0,0.1)}.errorMessage{color:red;font-size:xx-small}.ftco-navbar-light{background:0 0 !important;position:absolute;top:20px;left:0;right:0;z-index:3}.navbar-brand{font-weight:900;font-size:26px;line-height:1}.navbar-brand i{font-style:normal;color:#ffd369}.ftco-navbar-light .navbar-nav>.nav-item>.nav-link{font-size:18px;padding-top:.9rem;padding-bottom:.9rem;padding-left:20px;padding-right:20px;color:rgba(255,255,255,0.8);font-weight:400;opacity:1 !important}li a:hover{color:#ffd369 !important}.blog-entry{overflow:hidden}.blog-entry .text{position:relative;width:100%;margin:0 auto}.block-20{overflow:hidden;background-size:cover;background-repeat:no-repeat;background-position:center center;position:relative;display:block;width:100%;height:400px;border-radius:4px}.blog-entry .text .topper{margin-top:-40px;position:absolute;top:0;left:20px;background:#ffd369}.blog-entry .text .topper:after{position:absolute;bottom:-10px;left:40px;content:'';width:0;height:0;border-style:solid;border-width:10px 10px 0;border-color:#ffd369 transparent transparent transparent}.blog-entry .text .heading{font-size:22px;font-weight:800}.blog-entry .text .btn-custom{color:rgba(0,0,0,0.4);text-transform:uppercase;font-size:12px;letter-spacing:1px}.ftco-section{padding:7em 0;position:relative}.case{width:100%;margin-bottom:50px}.case .img{height:400px}.case .text h2{color:#000}.case .text h2{font-size:40px;font-weight:700;margin-bottom:30px}.case .text .media-social li{list-style:none;margin:0 5px 0 0;display:inline-block}.icon-share:hover{background:#ffd369;border-radius:5px}.no-gutters{margin-right:0;margin-left:0}.blog-title:hover{color:gray !important}.cover-img:hover{-webkit-filter:brightness(90%) !important;filter:brightness(90%) !important}@media (max-width: 1199.98px){.case .text .media-social{opacity:1}}@media screen and (min-width: 1367px){.signup-page{height:100vh}}@media screen and (max-width: 756px){.ftco-navbar-light{background:#000 !important;position:relative;top:0}.blog-entry{margin-bottom:30px}}

.signup-page{position:relative}.no-gutters{margin-right:0;margin-left:0}.slider-text h1{font-size:10vw;color:#fff;line-height:1.2;font-weight:700}.slider-text .subHeading{font-size:18px;color:#fff;font-weight:400;margin-bottom:0}.slider-text .text{position:relative}.slider-text .breadcrumbs{font-size:16px;font-weight:400;margin-bottom:20px;z-index:99}.slider-text p{line-height:1.5;color:rgba(255,255,255,0.9)}.mouse{position:absolute;left:0;bottom:-100px;z-index:2}.mouse-icon{height:80px;border:1px solid transparent;background:0 0;cursor:pointer;position:relative;text-align:center;margin:0 auto;display:block}.mouse-wheel{height:80px;margin:0 auto;display:block;width:30px;background:0 0;border-radius:50%;-webkit-animation:1.6s ease infinite wheel-up-down;animation:1.6s ease infinite wheel-up-down;color:#fff;font-size:50px;line-height:1}@media (max-width: 1199.98px){.signup-page{background-position:center center !important}.slider-text h1{font-size:14vw}}

.ftco-footer{font-size:16px;background:#222831;padding:5em 0 3em}.ftco-footer .logo{font-size:34px !important;font-weight:800 !important;line-height:1}.ftco-footer .ftco-footer-widget h2{color:#fff;margin-bottom:40px}.ftco-footer p{color:rgba(255,255,255,0.7)}.ftco-footer-social li{list-style:none;margin:0 10px 0 0;display:inline-block}img:hover{background:#ffd369;border-radius:15px;cursor:pointer}.ftco-footer .ftco-footer-widget h2{color:#fff;margin-bottom:40px;font-size:20px;font-weight:400}.block-foot ul li,.block-23 ul li>a{display:table;line-height:1.5;margin-bottom:15px}.block-foot ul li .icon{width:40px;font-size:18px;padding-top:2px;color:#fff}.block-foot ul li .icon,.block-foot ul li .text{display:table-cell;vertical-align:top}

